<template>
  <div class="right">
    <cpntShow>
      <template slot="cpnt">LuoSideNavgation</template>
      <template slot="more">
    <h2 class="showTitle">基础用法</h2>

        <luo-side-navigation
      :channels="channels"
      layout-way="col"
      width="100%"
      height="23vh"
      :active-channel="activeChannel"
      @to-channel="toChannel"
    />
    <h2 class="showTitle">backgroundColor改变背景颜色</h2>
    <luo-side-navigation
      :channels="channels"
      layout-way="col"
      width="100%"
      height="23vh"
      backgroundColor="aliceblue"
      :active-channel="activeChannel"
      @to-channel="toChannel"
    />
    <h2 class="showTitle">backgroundActiveColor改变选中背景颜色</h2>
    <luo-side-navigation
      :channels="channels"
      layout-way="col"
      width="100%"
      height="23vh"
      backgroundActiveColor="aliceblue"
      :active-channel="activeChannel"
      @to-channel="toChannel"
    />
      </template>

    </cpntShow>
  </div>
</template>

<script>
import cpntShow from '@/components/cpntShow.vue'
export default {
    components:{
      cpntShow
    },
    data () {
    return {
      channels: [
        {
          title: 'basicSettings',
          value: 'basic-settings'
        },
        {
          title: 'extraSettings',
          value: 'extra-settings'
        },
        {
          title: 'security',
          value: 'security'
        }
      ],
      activeChannel: ''
    }
  },
  methods: {
    toChannel (data) {
      this.activeChannel = data
    }
  }

}
</script>

